import React from "react";
import lottie from "lottie-web";
import Gatoso from "../../assets/cat.json"
import { Container, Heading, Section } from "./styles";

const Home = () => {
  const container = React.useRef();

  React.useEffect(() => {
    lottie.loadAnimation({
      container: container.current,
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: Gatoso,
    });
  }, []);

  return (
    <Section>
     <Heading>Gato Gatoso</Heading>
      <Container ref={container}></Container>
      <h2>Miaaaau</h2> <small>Por: Galv</small>
    </Section>
  );
};

export default Home;
