import styled from 'styled-components'

export const Section = styled.section`
  height: 100vh;
  width: 100%;
  display: flex;
  place-items: center;
  flex-direction: column;
`

export const Heading = styled.h1`
  font-size: 3rem;
  margin: 1rem 0;
  text-align: center;
`

export const Container = styled.div`

  display: block;
  height: 60vh;
  width: 100%;
`